import getField from './fieldMapping'

export default {
  name: 'GenericInput',
  functional: true,
  props: {
    name: {},
    type: {
      type: String, // editor
      default: 'string'
    },
    value: {},  // 
    static: {
      type: Boolean,
      default: false
    }, //
    options: Object, // 
    multiple: {
      type: Boolean,
      default: false
    },
    format: {}, //
    placeholder: {},
    info: {},
    required: {
      type: Boolean,
      default: false
    },
    staticSuffix: {
      type: String,
      default: '_static'
    }
  },

  render (h, ctx) {
    let self = ctx.props
    let static_name = `${self.name}${self.staticSuffix}`
    let Input = getField(self.type)
    let input = new Input.field(ctx.parent, ctx.props)
    if (self.static) {
      //name_staticgetStaticValue
      let v = self.value[`${self.name}${self.staticSuffix}`]
      if (!v)
        input.setStaticValue(self.value[self.name])
    }
    if (self.static) {
      return h(Input.static, {props: ctx.props})
    } else {
      return input.render(h, ctx)
    }
  }
}



